export const BASE_URL = 'https://digitalsavertest.azurewebsites.net/api';

function url(path, base = BASE_URL) {
    return base + path;
}

export const galleryUrl = () => url('/gallery/Gallery');
export const networkRegionFiltersUrl = () => url('/gallery/NetworkRegionFilters');
export const loginUrl = () => url('/auth/login');
export const registerUserUrl = () => url('/account/RegisterUser');
export const forgotPasswordUrl = () => url('/account/ForgotPassword');
export const getOfferPDFUrl = (digitalOfferId, networkId) => url(`/digitalOffers/GetOfferPDF?digitalOfferId=${digitalOfferId}&networkId=${networkId}`);
export const generateCartUrl = () => url('/digitalOffers/GenerateCart');
export const networksPointsUrl = () => url('/gallery/NetworkNetworkPoints');
export const informBEAboutGoogleLoginUrl = () => url('/ExternalAuth/GoogleAuth');
export const informBEAboutFacebookLoginUrl = () => url('/ExternalAuth/FacebookAuth');




