import {downloadFile} from "../utils/download-file";
import { getOfferPDFUrl } from "../data/urls";

export const DownloadFileMixin = {
    data() {
      return {
          loading: false,
      };
    },
    computed: {
        pdfUrl() {
            return getOfferPDFUrl(this.offer.offerId, this.offer.networkIds[0]);
        },
    },
    methods: {
        downloadFile() {
            this.loading = true;

            downloadFile(this.pdfUrl)
                .then(() => (this.loading = false));
        }
    }
};
