<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <modal class="adv-g__map-modal" v-on:close="$emit('close')">
        <template v-slot:title>
            <heading size="h1">
                {{i18n.map.mapHeader}}
            </heading>
        </template>
        <template v-slot:body>
            <div class="adv-g__map-modal__body">
                <div class="adv-g__map-modal__body__filters">
                    <multiselect
                            v-bind:options="shownNetworks"
                            class="adv-g__map-modal__body__networks"
                            v-model="selectedNetworks"
                            v-bind:option-title-fn="networkIdToTitleFn"
                            v-bind:placeholder="i18n.map.selectNetworksPlaceholder"
                    ></multiselect>
                    <autocomplete
                            v-bind:options="availableRegions"
                            class="adv-g__map-modal__body__region"
                            v-model="selectedRegion"
                            v-bind:placeholder="i18n.map.selectRegionPlaceholder"
                    ></autocomplete>
                </div>
                <maps
                        class="adv-g__map-modal__map"
                        v-bind:points="filteredPoints"
                ></maps>
            </div>
        </template>
    </modal>
</template>

<script>
    import Modal from "../../ui/Modal";
    import Heading from "../../ui/Heading";
    import Maps from "./Maps";
    import * as http from '../../../utils/http';
    import { flatten } from "../../../utils/array";
    import Autocomplete from "../../ui/Autocomplete";
    import Multiselect from "../../ui/Multiselect";
    import { FilterMixin } from "../../../mixins/Filter";
    import { i18nAwareMixin } from "../../../mixins/i18n/I18nAware";
    import { networksPointsUrl } from "../../../data/urls";

    export default {
        name: "MapModal",
        components: {Multiselect, Autocomplete, Maps, Heading, Modal},
        mixins: [FilterMixin, i18nAwareMixin],
        props: {
            offer: {
                type: Object,
                required: true
            }
        },
        data() {
            return {
                points: []
            };
        },
        mounted() {
            this.loadNetworksPoints();
        },
        computed: {
            filteredPoints() {
                const {selectedNetworks, availableNetworks, points} = this;
                const networksToFilterBy = selectedNetworks.length ? selectedNetworks : availableNetworks;
                const networksToFilterByMap = networksToFilterBy.reduce((acc, id) => ({...acc, [id]: true}), {});

                return points.filter(({networkId}) => networksToFilterByMap[networkId]);
            },
            shownNetworks() {
                const {points} = this;
                const networksMap = points.reduce((acc, {networkId}) => ({...acc, [networkId]: true}), {});

                return this.availableNetworks.filter(id => networksMap[id]);
            }
        },
        methods: {
            loadNetworksPoints() {
                Promise.all(
                    this.offer.networkIds.map(id => this.loadPointForOneNetwork(id))
                )
                    .then(flatten)
                    .then(points => this.points = points);
            },
            loadPointForOneNetwork(networkId) {
                return http.get(networksPointsUrl(), {
                    id: networkId
                });
            }
        }
    }
</script>

<style scoped>
    .adv-g__map-modal {
        width: 800px;
        max-width: 97vw;
        height: 600px;
        position: relative;
    }

    .adv-g__map-modal__body {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    .adv-g__map-modal__body__filters {
        display: flex;
        justify-content: space-between;
        z-index: 2;
        padding-bottom: 24px;
        padding-top: 2px;
    }

    .adv-g__map-modal__map {
        z-index: 1;
        position: relative;
        min-height: 390px;
        max-height: 396px;
        border-radius: 4px;
    }

    .adv-g__map-modal__body__networks {
        flex-grow: 1;
        margin-right: 10px;
        max-width: 45vw;
        z-index: 3;
    }

    .adv-g__map-modal__body__region {
        flex-grow: 1;
        margin-left: 10px;
        z-index: 2;
    }

    @media screen and (max-width: 500px) {
        .adv-g__map-modal__body__filters {
            flex-direction: column;
            justify-content: stretch;
        }

        .adv-g__map-modal__body__networks {
            margin-right: 0;
            margin-bottom: 10px;
            max-width: initial;
        }

        .adv-g__map-modal__body__region {
            margin-left: 0;
        }
    }

    @media screen and (max-width: 500px) {
        .adv-g__map-modal {
            height: 680px;
        }
    }
</style>
